import React from 'react';
import './Video.scss';
import {defaultValues} from '../../constants';


const defaultProps = {
    title: defaultValues.DESCRIPTION,
    show: true,
    src: defaultValues.VIDEO_RECTANGLE 
};

const Video = (props) => {
    let {
        className,
        style,
        title,
        show,
        src
    } = props
    return(
        <div className={'video video-sm' + (className || '') } style={style} >
            <video controls src={src} alt={title + ' Video'}/>
            {show ? <p className="Body 1 Caption">{title}</p> : ''}
        </div>
    )
}

Video.defaultProps = defaultProps;

export default Video;